import Page from 'components/Page';
import React from 'react';
import {
  Button,
  Card,
  CardBody,
  CardHeader,
  Col,
  Form,
  FormFeedback,
  FormGroup,
  FormText,
  Input,
  Label,
  Row,
  Alert,
  Table,
} from 'reactstrap';
import { newTruck, findTrucks, findOwners} from "../api/api";
import AsyncStorage from '@callstack/async-storage';
import conf from "../api/config";
import MUIDataTable from "mui-datatables";
import CustomToolbarSelectTrucks from "../components/CustomToolbarSelectTrucks";
class FormPage extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
          number : '',
          owner : 'nn',
          make : '',
          vin : '',
          insurance_expires:'',
          inspection_expires:'',
          registration_expires:'',
          xtoken:'',
          msg: '',
          error: '',
          downloadFile:true,
          data: [],
          owners:[],
        };
        AsyncStorage.getItem(conf.secret)
        .then((results) => {
          if( results !== null )
          {
            let obje_results = JSON.parse(results);
            this.setState({
              ...this.state,
              xtoken : obje_results.user.token,
            });
            findTrucks(obje_results.user.token).then((response) => {
                this.setState({
                    ...this.state,
                    data:response.data,
                });
            }).catch((respError) => {});

            findOwners(obje_results.user.token).then((ownerResponse) => {
              this.setState({
                  ...this.state,
                  owners:ownerResponse.data,
              });
            }).catch((ownerRespError) => {});

          }
        })
        .catch((error) => {
          console.log('storage error', error);
        });
    }
    componentDidMount() {
    }

    rerenderParentCallback = () => {
      findTrucks(this.state.xtoken).then((response) => {
        this.setState({
            ...this.state,
            data:response.data,
        });
      }).catch((respError) => {
      });
    }
    
    handleNumberInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, number: v,});
    }
    handleOwnerInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, owner: v,});
    }
    handleMakeInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, make: v,});
    }
    handleVinInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, vin: v,});
    }
    handleInsExpInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, insurance_expires: v,});
    }
    handleInspeExpInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, inspection_expires: v,});
    }
    handleRegExpInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, registration_expires: v,});
    }
    handleSaveChanges = () => {
        // console.log('so far', this.state);
        newTruck(this.state.xtoken, this.state)
        .then((response) => {
            if( response.status === 200)
            {
                this.setState({
                    ...this.state,
                    number : '',
                    owner : 'nn',
                    make : '',
                    vin : '',
                    insurance_expires:'',
                    inspection_expires:'',
                    registration_expires:'',
                    msg: response.message,
                    data: response.data,
                    error: '',
                });
                this.forceUpdate();
            }
            else
            {
                this.setState({
                    ...this.state,
                    error: response.message,
                    msg: '',
                });
            }
        })
        .catch((error) => {
            this.setState({
                ...this.state,
                error: 'Connection error occured',
            });
            console.log('error', error);
        });
    }
    render() {
        const columns = [
            {
              name: 'Id',
              options: {
                display: false
              }
            },
            {
              name: 'Oid',
              options: {
                display: false
              }
            },
            'Truck No.',
            'Truck Owner',
            'Truck Make',
            'Truck VIN',
            'Insurance Expiry',
            'Inspection Expiry',
            'Registration Expiry',
        ];

        const data2 = () => {
          let rtn = [];
          this.state.data.map(item => {
            rtn.push([
              item.id,
              item.owner,
              item.number,
              item.company,
              item.make,
              item.vin,
              item.insurance_expires,
              item.inspection_expires,
              item.registration_expires,
            ]);
          });
          return rtn;
        }
      
        const options = {
            filter: true,
            selectableRows: 'single',
            selectableRowsOnClick:true,
            filterType: 'dropdown',
            responsive: 'vertical',
            draggableColumns: {
              enabled: true
            },
            rowsPerPage: 5,
            selectToolbarPlacement: 'replace',
            customToolbarSelect: (selectedRows, displayData, setSelectedRows) => (
              <CustomToolbarSelectTrucks rerenderParentCallback={this.rerenderParentCallback} selectedRows={selectedRows} displayData={displayData} setSelectedRows={setSelectedRows}  originalData={this.state.data} Xtoken={this.state.xtoken}/>
            ),
            downloadOptions: {
                filename: 'trucks-excel-format.csv',
                separator: ';',
                filterOptions: {
                  useDisplayedColumnsOnly: true,
                  useDisplayedRowsOnly: true,
                }
            },
            onDownload: (buildHead, buildBody, columns, data2) => {
              if (this.state.downloadFile) {
                let val= `${buildHead(columns)}${buildBody(data2)}`.trim();
                return val;
              }
      
              return false;
            },
            onRowSelectionChange: (currentRowsSelected, allRows, rowsSelected) => {
              // console.log('currentRowsSelected', currentRowsSelected);
            },
            onRowsDelete: rowsDeleted => {
              // console.log(rowsDeleted, 'were deleted!');
            },
            onChangePage: numberRows => {
              // console.log(numberRows);
            },
            onSearchChange: searchText => {
              // console.log(searchText);
            },
            onColumnSortChange: (column, direction) => {
              // console.log(column, direction);
            },
            onViewColumnsChange: (column, action) => {
              // console.log(column, action);
            },
            onFilterChange: (column, filters) => {
              // console.log(column, filters);
            },
            onCellClick: (cellIndex, rowIndex) => {
              // console.log(cellIndex, rowIndex);
            },
            onRowClick: (rowData, rowState) => {
              // console.log(rowData, rowState);
            },
        };
      return (
        <Page title="Trucks" breadcrumbs={[{ name: 'Trucks', active: true }]}>
          <Row>
            <Col xl={12} lg={12} md={12}>
              <Card>
                <CardHeader>Create Truck</CardHeader>
                {this.state.error.length > 0 && (
                    <Alert color="danger">{this.state.error}</Alert>
                )}
                {this.state.msg.length > 0 && (
                    <Alert color="success">{this.state.msg}</Alert>
                )}
              </Card>
            </Col>
          </Row>

          <Row>
            <Col xl={4} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="exampleEmail">Truck Reg. Number*</Label>
                      <Input onChange={this.handleNumberInput}
                        type="text" 
                        value={this.state.number}
                        name="number"
                        placeholder=""
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="exampleEmail">Truck Owner*</Label>
                      <Input onChange={this.handleOwnerInput} value={this.state.owner} defaultValue={this.state.owner} type="select" name="owner">
                        <option value="nn" disabled={true}>Select owner</option>
                        {
                          this.state.owners.map((stt, idx) => (
                            <option key={idx} value={stt.id}>{stt.company}</option>
                          ))
                        }
                      </Input>
                    </FormGroup>
                    <FormGroup>
                      <Label for="exampleEmail">Truck Make*</Label>
                      <Input onChange={this.handleMakeInput}
                        type="text" 
                        value={this.state.make}
                        name="make"
                        placeholder=""
                      />
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>
            <Col xl={4} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="examplePassword">Truck VIN*</Label>
                      <Input onChange={this.handleVinInput}
                        type="text"
                        value={this.state.vin}
                        name="vin"
                        placeholder=""
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="exampleEmail">Insurance Expiry*</Label>
                      <Input onChange={this.handleInsExpInput}
                        type="date" 
                        value={this.state.insurance_expires}
                        name="make"
                        placeholder=""
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="exampleEmail">Inspection Expiry*</Label>
                      <Input onChange={this.handleInspeExpInput}
                        type="date" 
                        value={this.state.inspection_expires}
                        name="make"
                        placeholder=""
                      />
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>

            <Col xl={4} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="exampleEmail">Registration Expiry*</Label>
                      <Input onChange={this.handleRegExpInput}
                        type="date" 
                        value={this.state.registration_expires}
                        name="make"
                        placeholder=""
                      />
                    </FormGroup>
                    <FormGroup check row>
                      <Col sm={{ size: 10, offset: 2 }}>
                        <Button type="button" onClick={this.handleSaveChanges}>Add truck</Button>
                      </Col>
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>
          </Row>

          <Row>
            <Col xl={12} lg={12} md={12}>
              <Card>
                <CardBody>
                    <MUIDataTable title={'Trucks'}
                    data={this.state.data.map(item => {
                      return [
                        item.id,
                        item.owner,
                        item.number,
                        item.company,
                        item.make,
                        item.vin,
                        item.insurance_expires,
                        item.inspection_expires,
                        item.registration_expires,
                      ]
                    })} columns={columns} options={options} />
                </CardBody>
              </Card>
            </Col>
          </Row>
    
        </Page>
      );
    }
}
export default FormPage;
